import React from 'react';
import { Link,  useNavigate } from 'react-router-dom';

function myParams(Component) {
    return props => <Component navHook={useNavigate()} />;
}
class SignIn extends React.Component {
    state = {
        username: "",
        password: ""
    };
    user = {}
    reviewUser = () => {
        this.user = JSON.parse(localStorage.getItem('users'));
        if (this.user.username == this.state.username
            && this.user.password == this.state.password) {
            console.log(this.user);
            this.props.navHook('/profile')
        }
        else {
            console.log("failde");
        }
    }

    render() {
        return (
            <div className='container'>
                <h1></h1>
                <div className='form'>
                <div className='name'>
                    <label htmlFor="username">Name</label>
                    <input type="text"
                        onChange={(e) => { this.setState({ username: e.target.value }) }} />
                </div>
                <div className="password">
                    <label htmlFor="password">Password</label>
                    <input type="password"
                        onChange={(e) => { this.setState({ password: e.target.value }) }} />
                </div>
                <div className='button'>
                <button type='button' onClick={() => { this.reviewUser() }}>Sign up</button>
                </div>
                </div>
                {/* <div class="signUp" >
                    <p> ?  <Link className='link' to="login" replace>Sign up </Link></p>
                </div> */}
            </div>
        )
    }
}
export default myParams(SignIn)