import React from 'react';
import {
    Routes,
    Route, Link
} from 'react-router-dom'

import Home from './Home';
import Login from './Login';
import News from './News';
import SignIn from './SignIn';
import Profile from './Profile'
import NewsDatails from './NewsDatails';;

class Menu extends React.Component {
    render() {
        return (
            <>
                <div className="menu">
                    <Link className="link" to="/">Home</Link>
                    <Link className="link" to="login">Login</Link>
                    <Link className="link" to="news">News</Link>
                    <Link className="link" to="signIn">Profile</Link>
                </div>
                <Routes>
                    <Route path="/" element={<Home />} />
                    <Route path="login" element={<Login />} />
                    <Route path="news">
                    <Route path="" element={<News />} />
                        <Route path="newsDatails" element={<NewsDatails />} />
                        <Route path="newsDatails/:id" element={<NewsDatails />} />
                        </Route>
                    <Route path="signIn" element={<SignIn />} />
                    <Route path="profile" element={<Profile />} />
                </Routes>
            </>
        )
    }
}
export default Menu